.about
  min-height: 634px 

  &__container    

  &__body    
    display: flex
    align-items: flex-start
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    padding: 56px

  &__video
    position: relative
    flex: 1 1 40.62%
    height: 522px
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    margin-right: 48px

  &__content
    flex: 1 1 43.9%
    height: 522px
    display: flex
    flex-direction: column

.about-video

  &__label
    display: block
    position: absolute
    top: -24px
    left: -24px
    width: 104px
    height: 104px
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    transform: rotate(-7deg)

  &__btn
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    width: 80px
    height: 80px
    border-radius: 100%
    background-color: rgba(1, 1, 1, 0.7)
    svg
      width: 12px
      height: 12px
      fill: var(--main-white)

  &__descr
    display: block
    position: absolute
    bottom: 13px
    left: 50%
    transform: translateX(-50%)
    text-align: center
    color: var(--main-white)
    max-width: 160px

.about-content

  &__title
    margin-bottom: 16px

  &__descr
    flex: 1 1 auto
    margin-bottom: 16px

  &__list
    display: grid
    grid-template-columns: repeat(3, 1fr)
    gap: 8px

  &__column
    min-height: 200px
    display: flex
    flex-direction: column
    justify-content: space-between
    border-radius: 8px
    background-color: var(--main-white)
    padding: 16px 24px

.about-column

  &__num
    display: flex
    align-items: center
    font-family: var(--secondary-font-family)
    font-size: 48px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.48px
    span
      display: block
      margin-right: 11px

  &__icons
    min-width: 0
    flex: 0 0 34px
    height: 40px

  &__slider
    pointer-events: none
    width: 100%
    height: 100%

  &__icon
    opacity: 0
    visibility: hidden
    background-position: center
    background-repeat: no-repeat
    background-size: 34px 40px
    &.swiper-slide-active
      opacity: 1
      visibility: visible

