.subcategories
  min-height: 232px

  &__item  
    display: flex
    flex-direction: column
    align-items: center  

  &__img
    display: block
    width: 120px
    flex: 0 0 120px
    border-radius: 100%    
    background-color: #F3F0ED
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    margin-bottom: 16px    
      
  &__name
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px




