.interiors
  min-height: 854px

  &__container

  &__top
    display: flex
    align-items: flex-end
    margin-bottom: 32px

  &__left
    flex: 1 1 auto
    margin-right: 15px

  &__title
    margin-bottom: 16px

  &__descr
    line-height: 160%    
    max-width: 930px

  &__slider    

  &__body
    width: 100%
    height: 100%

  &__wrapper
    margin-bottom: 32px

  &__slide
    position: relative
    height: 570px
    background-position: center
    background-repeat: no-repeat
    background-size: cover

  &__pagination.swiper-horizontal>.swiper-pagination-progressbar, .swiper-pagination-progressbar.swiper-pagination-horizontal
    width: 100%  

.interiors-slide

  &__point
    position: absolute   

    &--1      
      top: 360px
      left: 332px

    &--2
      top: 310px
      right: 134px

    &--3
      top: 310px
      right: 234px

    &--4
      top: 280px
      right: 174px

  &__btn
    position: relative
    width: 48px
    height: 48px    
    border-radius: 100%
    background-color: rgba(255, 255, 255, 0.3)
    transition: background-color 0.3s ease 
    &::before
      position: absolute
      content: ''
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)
      width: 24px
      height: 24px
      border-radius: 100%
      background-color: var(--main-white)    
    @media (any-hover: hover)
      &:hover
        background-color: rgba(51, 51, 51, 0.5)
        &::before
          width: 16px
          height: 16px
          transition: width 0.3s ease, height 0.3s ease         
        
    
  &__info
    position: absolute
    top: -251px
    left: -292px
    opacity: 0
    visibility: hidden
    transition: opacity 0.4s ease, visability 0.3s ease
    &.active      
      opacity: 1
      visibility: visible

    .product-item__content
      padding: 4px 16px 16px 16px 
    

