.description

  &__container
    padding: 56px 0 
    border-top: 1px solid  rgba(51, 51, 51, 0.08)
    border-bottom: 1px solid rgba(51, 51, 51, 0.08)

  &__title
    margin-bottom: 24px

  &__content    
    margin-bottom: 16px

  &__text
    color: var(--main-gray)
    line-height: 160%
    &:not(&:last-child)
      margin-bottom: 12px    

  &__btn
    display: flex
    align-items: center    
    span
      display: block
      color: var(--main-blue)
      margin-right: 2px
      transition: color 0.3s ease
    svg
      width: 18px
      height: 18px
      fill: var(--main-blue)
      transition: fill 0.3s ease
    @media (any-hover: hover)
      &:hover
        span
          color: var(--main-electric-blue)
        svg
          fill: var(--main-electric-blue)
    
      
