.header 
  min-height: 208px     
  width: 100%
  position: absolute
  left: 0
  top: 0
  z-index: 500  

  &__special
    display: block
    text-align: center
    background-color: var(--main-blue)
    color: var(--main-white)    
    font-size: 13px    
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.13px
    padding: 4px 0

  &__container
    background-color: var(--main-white)

  &__top
    display: flex
    align-items: center
    padding: 12px 0
    border-bottom: 1px solid var(--border-color)

  &__center    
    position: relative
    display: flex
    align-items: center
    padding: 23px 0
  
  &__bottom
    position: relative

.top-header

  &__location
    display: flex
    align-items: center
    font-size: 13px    
    font-weight: 500    
    letter-spacing: -0.13px    
    @media (any-hover: hover)
      &:hover
        .top-header__city
          color: var(--main-blue)
    
    svg
      width: 16px
      height: 16px
      fill: var(--main-blue)
      margin-right: 4px

  &__city    
    margin-right: 9px
    transition: color 0.3s ease

  &__quantity
    position: relative
    color: var(--light-gray) 
    white-space: nowrap
    margin-right: 32px
    &::before
      position: absolute
      content: ''
      top: 50%
      left: -5px
      transform: translateY(-50%)
      width: 2px
      height: 2px
      border-radius: 100%
      background-color: var(--light-gray)

  &__menu
    margin-right: 69px

  &__list
    display: flex
    align-items: center

  &__item
    &:not(&:last-child)
      margin-right: 32px

    &--submenu
      position: relative

  &__link
    font-size: 13px
    font-weight: 500    
    letter-spacing: -0.13px  
    line-height: 120%
    white-space: nowrap  
    transition: color 0.3s ease      
    @media (any-hover: hover)
      &:hover
        color: var(--main-blue)

        span          
          color: var(--main-blue)
        svg
          fill: var(--main-blue)

    &--element
      position: relative
      padding-left: 20px
      &::before
        content: ''
        position: absolute
        top: 50%
        transform: translateY(-50%)
        left: 2px
        width: 12px
        height: 12px
        border-radius: 100%
        background-color: rgba(212, 74, 27, 0.2)

      &::after
        content: ''
        position: absolute
        top: 50%
        transform: translateY(-50%)
        left: 6px
        width: 4px
        height: 4px
        border-radius: 100%
        background-color: var(--accent-dark-orange)

    &--submenu      
      display: flex
      align-items: center
      line-height: 110%
      span
        color: var(--light-gray)
        margin-right: 2px
        transition: color 0.3s ease        

      svg
        width: 16px
        height: 16px
        fill: var(--light-gray)
        transition: fill 0.3s ease, transform 0.3s ease   

      &.active
        span          
          color: var(--main-blue)       
          
        svg
          transform: rotate(-180deg) 
          fill: var(--main-blue)

  &__contacts
    top: 24px
    right: 0

  &__submenu    
    top: 24px
    left: 0    
    
  &__sublist
    display: flex
    flex-direction: column
    align-items: flex-start
    padding: 8px

  &__subitem
    width: 100%    
    &:not(&:last-child)
      margin-bottom: 8px

  &__sublink
    display: block
    width: 100%
    text-align: left
    white-space: nowrap
    padding: 5px 19px    
    font-size: 13px
    font-weight: 500
    letter-spacing: -0.13px
    border-radius: 4px
    transition: background-color 0.3s ease
    @media (any-hover: hover)
      &:hover    
        background-color: rgba(51, 51, 51, 0.08)

  &__phones
    position: relative
    margin-right: 24px    

  &__phones-btn
    display: flex
    align-items: center
    &.active
      span          
        color: var(--main-blue)
      svg
        transform: rotate(-180deg) 
        fill: var(--main-blue)    
    @media (any-hover: hover)
      &:hover 
        span          
          color: var(--main-blue)
        svg
          fill: var(--main-blue)

    span
      color: var(--dark-color)
      font-weight: 500      
      letter-spacing: -0.16px
      margin-right: 2px
      transition: color 0.3s ease      

    svg
      width: 16px
      height: 16px
      fill: var(--dark-color)
      transition: fill 0.3s ease, transform 0.3s ease     

  &__callback
    margin-left: auto
    padding: 6px 15px
    color: var(--main-blue)
    font-size: 13px    
    font-weight: 500   
    letter-spacing: -0.13px
    border-radius: 999px
    border: 1.5px solid rgba(10, 72, 129, 0.20)
    transition: border-color 0.3s ease 0s
    &.disabled
      border-color: rgba(10, 72, 129, 0.30)
      cursor: not-allowed
      opacity: 0.5
    @media (any-hover: hover)
      &:hover
        border-color: var(--main-blue)

.center-header

  &__burger
    display: none

  &__logo
    display: block
    flex: 0 0 212px
    margin-right: 24px
    img
      width: 100%
      height: 100%
      object-fit: cover

  &__catalog
    margin-right: 8px    

  &__search
    position: relative    
    margin-right: 8px 
    &.active
      position: fixed
      width: 100%
      height: 100% 

  &__favorite,
  &__compare,
  &__cart
    flex: 0 0 56px
    height: 56px
    margin-right: 8px
    border-radius: 8px
    background-color: rgba(51, 51, 51, 0.08)
    transition: background-color 0.3s ease
    svg
      width: 24px
      height: 24px
      fill: var(--dark-color) 
      transition: fill 0.3s ease    
    @media (any-hover: hover)
      &:hover
        background-color: rgba(51, 51, 51, 0.16)
        svg
          fill: var(--main-blue)  

  &__cart    
    position: relative
    span
      position: absolute      
      top: 5px
      right: 5px
      display: flex
      align-items: center
      justify-content: center
      font-size: 11px
      font-weight: 500
      letter-spacing: -0.13px
      width: 21px
      height: 21px
      border-radius: 100%
      background-color: var(--main-blue)
      color: var(--main-white)
      border: 2px solid #efefef
      transition: border-color 0.3s ease 0s
    @media (any-hover: hover)
      &:hover
        span
          border-color: #dedede

  &__account
    font-weight: 500
    letter-spacing: -0.16px
    padding: 20px 32px
    border-radius: 8px
    background-color: rgba(51, 51, 51, 0.08)
    transition: background-color 0.3s ease, color 0.3s ease    

    @media (any-hover: hover)
      &:hover
        color: var(--main-blue)
        background-color: rgba(51, 51, 51, 0.16)

.catalog-header

  &__btn
    background-color: var(--main-blue)
    padding: 16px 32px 16px 25px
    border-radius: 8px
    span
      color: var(--main-white)
      font-weight: 500    
      letter-spacing: -0.16px
    &.active
      .catalog-header__svg-open
        display: none

      .catalog-header__svg-close
        display: block

  &__svg-open,
  &__svg-close  
    width: 24px
    height: 24px
    fill: var(--main-white)
    margin-right: 8px

  &__svg-close
    padding: 4px
    display: none

  &__wrapper
    width: 100%
    top: 89px 
    left: 0
    display: flex
    align-items: flex-start        

  &__left
    flex: 0 0 224px    
    padding: 32px 0
    background-color: var(--light-beidge)

  &__right
    flex: 1 1 auto
    display: flex
    align-items: flex-start
    justify-content: space-between
    padding: 40px 0px 40px 24px

  &__tab-btn
    width: 100%
    text-align: left
    padding: 16px 
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px
    transition: background-color 0.3s ease
    &.active
      background-color: var(--main-white)

    @media (any-hover: hover)
      &:hover
        background-color: var(--main-white)

  &__body
    display: grid
    grid-template-columns: repeat(3, 205px)
    column-gap: 24px
    row-gap: 40px

  &__category
    display: block
    color: var(--light-gray)    
    font-size: 13px
    font-weight: 500    
    letter-spacing: -0.13px
    line-height: 120%
    margin-bottom: 16px

  &__subitem
    &:not(&:last-child)
      margin-bottom: 15px

  &__sublink
    line-height: 160%
    transition: color 0.3s ease 
    @media (any-hover: hover)
      &:hover
        color: var(--main-blue)

.search-header

  &__form
    width: 519px    
    min-height: 56px 
    &.active
      .search-form__icon
        display: none
      .search-form__btn--search
        display: flex
      .search-form__btn--clear
        display: flex
        margin-right: 20px      

  &__btn-mobile
    display: none  

  &__result
    opacity: 0
    visibility: hidden
    position: absolute
    width: 100%
    top: 66px
    left: 0
    padding: 24px
    background-color: var(--main-white)
    border-radius: 8px    
    transition: opacity 0.4s ease, visibility 0.4s ease
    z-index: 100  
    &.active      
      opacity: 1
      visibility: visible

.search-form
  padding: 3px 3px 3px 16px
  display: flex
  align-items: center
  border: 2px solid rgba(51, 51, 51, 0.08)
  background-color: var(--main-white)
  border-radius: 8px
  transition: border-color 0.3s ease
  &:focus-within
    border-color: var(--dark-color)

  &__icon
    margin-right: 8px
    svg
      width: 24px
      height: 24px
      fill: var(--light-gray)

  &__btn 

    &--search   
      display: none
      color: var(--main-white)
      border-radius: 6px
      background-color: var(--main-blue)
      font-weight: 500
      line-height: 120%
      letter-spacing: -0.16px
      padding: 13px 33px
      svg
        display: none
        width: 24px
        height: 24px
        fill: var(--light-gray) 
        
    &--clear
      display: none
      svg
        width: 18px
        height: 18px
        fill: var(--light-gray)

      &.active
        display: flex         

  &__label
    width: 100%

  &__input
    width: 100%
    color: var(--dark-color)
    font-weight: 500    
    letter-spacing: -0.16px
    outline: none
    padding-right: 8px
    &::placeholder
      color: var(--light-gray)
      font-weight: 500      
      letter-spacing: -0.16px

.search-result

  &__top
    border-bottom: 1px solid rgba(51, 51, 51, 0.08)
    padding-bottom: 18px

  &__center

  &__bottom
    padding: 16px 0

  &__all
    position: relative
    margin-top: 24px
    display: inline-block
    font-size: 13px
    line-height: 120%
    letter-spacing: -0.13px
    color: var(--main-blue)
    &::before
      position: absolute
      content: ''
      bottom: -1px
      left: 0
      width: 100%
      height: 1px
      background-color: var(--main-blue)

.top-result

  &__list
    display: flex
    align-items: center
    flex-wrap: wrap

  &__item    
    border-radius: 999px
    background: rgba(51, 51, 51, 0.08)
    margin-bottom: 6px
    &:not(&:last-child)
      margin-right: 6px
      
  &__link
    color:  var(--main-gray)
    font-size: 14px
    line-height: 160%
    padding: 4px 12px 

.center-result

  &__row
    padding: 16px 0
    border-bottom: 1px solid rgba(51, 51, 51, 0.08)

  &__title
    display: block
    font-size: 13px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.13px
    margin-bottom: 16px

  &__item
    &:not(&:last-child)
      margin-bottom: 8px

  &__link
    line-height: 160%

.bottom-result

  &__title
    display: block
    font-size: 13px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.13px
    margin-bottom: 16px

  &__item
    display: flex
    align-items: flex-start
    height: 100%

    &:not(&:last-child)
      margin-bottom: 12px

.product-result

  &__img
    flex: 0 0 125px
    height: 84px
    margin-right: 8px
    background-position: center
    background-repeat: no-repeat
    background-size: cover

  &__content
    flex: 1 1 auto

  &__name
    display: block
    max-width: 150px
    font-size: 14px
    line-height: 160%
    margin-bottom: 17px

  &__prices
    display: flex
    align-items: center

  &__price
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px

    &--new
      margin-right: 8px

    &--old
      color: var(--light-gray)
      font-weight: 400
      text-decoration: line-through

  &__btn
    width: 40px
    height: 40px
    border-radius: 8px
    background: var(--main-blue)
    svg
      flex: 0 0 24px
      height: 24px
      fill: var(--main-white)

.bottom-header

  &__list
    display: flex
    align-items: center
    justify-content: space-between

  &__item    

    &--submenu      

  &__link 
    font-weight: 500   
    letter-spacing: -0.16px
    border-radius: 8px
    padding: 14px 16px 14px 24px
    transition: background-color 0.3s ease, color 0.3s ease
    @media (any-hover: hover)
      &:hover
        background-color: #F2F2F2 
    
    
    &--submenu
      display: flex
      align-items: center
      svg
        width: 20px
        height: 20px
        fill: var(--dark-color)
        margin-left: 2px
        transition: transform 0.3s ease, fill 0.3s ease   

      &.active
        background-color: var(--main-blue)
        color: var(--main-white)
          
        svg
          fill: var(--main-white)
          transform: rotate(-180deg) translateY(-2px)            

    &--new
      color: var(--accent-dark-orange)
      padding: 14px 24px

  &__link-element
    position: relative
    color: var(--accent-light-green) 
    padding-left: 14px
    &::before
      position: absolute
      content: ''
      top: 50%
      transform: translateY(-50%)
      left: 6px
      width: 3px
      height: 3px
      border-radius: 100%
      background-color: var(--light-gray)

  &__wrapper   
    width: 100% 
    display: flex
    align-items: flex-start
    top: 68px 
    left: 0 
    padding: 16px

  &__body    
    display: grid
    grid-template-columns: repeat(4, 210px)  
    column-gap: 24px
    row-gap: 40px    

  &__category
    display: block
    color: var(--light-gray)    
    font-size: 13px
    font-weight: 500    
    letter-spacing: -0.13px
    line-height: 120%
    margin-bottom: 16px

  &__subitem
    &:not(&:last-child)
      margin-bottom: 15px

  &__sublink
    line-height: 160%
    transition: color 0.3s ease 
    @media (any-hover: hover)
      &:hover
        color: var(--main-blue)    

.header-submenu
  position: absolute  
  opacity: 0
  visibility: hidden
  border-radius: 6px
  background-color: var(--main-white)    
  box-shadow: 0px 16px 24px 0px #ECE5DF  
  transition: opacity 0.4s ease, visibility 0.4s ease
  z-index: 5
  &.active
    opacity: 1
    visibility: visible 

.banner-submenu
  width: 288px
  height: 320px
  border-radius: 8px
  background-position: center
  background-repeat: no-repeat
  background-size: cover

.contacts-header
  width: 392px
  padding: 24px
  border-radius: 8px
  background-color: #F2F2F2
  box-shadow: 0px 16px 24px 0px #ECE5DF

  &__top
    display: flex
    align-items: flex-start
    justify-content: space-between
    margin-bottom: 6px

  &__phone
    font-size: 24px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.24px

  &__close
    
    svg
      width: 18px
      height: 18px
      fill: var(--main-gray)

  &__descr
    max-width: 285px
    color: var(--main-gray)
    font-size: 14px
    line-height: 160%
    margin-bottom: 24px

  &__callback
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px
    border-radius: 8px
    padding: 10px 40px
    color: var(--main-white)
    background-color: var(--main-blue)
    margin-bottom: 24px

  &__bottom
    height: 32px
    display: flex
    align-items: center

  &__link    
    display: flex
    align-items: center
    height: 100%
    color: var(--main-gray)
    font-size: 14px
    line-height: 160%      
    background-position: 0px 0px
    background-repeat: no-repeat
    background-size: 32px 32px  
    padding-left: 40px

    &--telegram
      margin-right: 24px
      background-image: url('../img/icons/telegram.svg')

    &--whatsapp
      margin-right: 24px      
      background-image: url('../img/icons/whatsapp.svg')
      






    
  
        