.salons
  min-height: 806px
  background-color: var(--medium-beidge)

  &__title
    margin-bottom: 16px

  &__descr
    line-height: 160%
    margin-bottom: 32px

  &__body
    display: flex

  &__map
    position: relative
    display: flex
    align-items: center
    padding-left: 24px
    flex: 1 1 912px
    height: 520px
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    margin-right: 24px

  &__slider
    min-width: 0
    flex: 0 0 288px

.salons-map

  &__menu
    width: 376px
    height: 472px
    border-radius: 4px
    background-color: var(--main-white)
    padding: 16px 0px 0px 16px
    overflow: hidden

  &__location
    position: absolute
    svg
      width: 48px
      height: 48px
      fill: var(--main-blue)
      transition: fill 0.3s ease
    @media (any-hover: hover)
      &:hover
        svg        
          fill: var(--accent-light-orange)
    

    &--1
      top: 11px
      left: 435px

    &--2
      top: 123px
      left: 440px

    &--3
      top: 123px
      left: 654px

    &--4
      top: 198px
      left: 640px

    &--5
      top: 325px
      left: 421px

    &--6
      top: 471px
      left: 658px

    &--7
      top: 410px
      left: 780px

.salons-menu

  &__top
    display: flex    
    align-items: center
    justify-content: space-between
    margin-bottom: 24px
    padding-right: 16px

  &__select
    flex: 1 1 auto
    border-radius: 8px
    border: 1px solid rgba(51, 51, 51, 0.08)
    background-color: var(--main-white)
    padding: 16px
    margin-right: 8px
  
  &__close
    width: 48px
    height: 48px

  &__body  
    height: 100%
    display: flex
    flex-direction: column  
    padding-bottom: 16px    
    
  &__list    
    overflow: auto
    padding-bottom: 35px

  &__item
    padding-right: 16px
    &:not(&:last-child)
      margin-bottom: 23px

  &__link
    display: flex
    align-items: flex-start        
    @media (any-hover: hover)
      &:hover
        .salons-menu__icon
          background-color: var(--accent-light-orange)    

  &__icon    
    width: 24px
    height: 24px
    border-radius: 100%
    background-color: var(--main-blue)
    margin-right: 12px
    transition: background-color 0.3s ease
    svg
      fill: var(--main-white)

  &__info

  &__title
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px
    margin-bottom: 4px

  &__schedule
    display: block    

  &__extra    
    display: block
    color: var(--light-gray)

  &__details
    margin-top: 4px
    display: block
    color: var(--main-blue)

  .simplebar-content
    padding: 0px 0px 45px !important

.salons-slider

  &__body

  &__wrapper

  &__slide

  &__link


