.bestsellers

.product-slider

  &__top
    display: flex
    align-items: center
    justify-content: space-between
    margin-bottom: 32px

  &__btns
    display: flex
    align-items: center

  &__btn
    border: 1px solid  rgba(51, 51, 51, 0.08)
    background-color: rgba(255, 255, 255, 0.50)
    transition: border-color 0.3s ease
    svg
      fill: var(--light-gray)
      transition: fill 0.3s ease
    
    @media (any-hover: hover)
      &:hover
        border: 1px solid rgba(51, 51, 51, 0.16)
        background-color: var(--main-white)
        svg
          fill: var(--main-gray)    

    &--prev
      margin-right: 8px

  &__slider
    margin-right: -56px

  &__wrapper
    margin-bottom: 32px
    align-items: stretch

  &__slide
    height: auto 

  &__pagination.swiper-horizontal>.swiper-pagination-progressbar, .swiper-pagination-progressbar.swiper-pagination-horizontal
    width: calc(100% - 56px)
  




