.promo
    
  &__container
    height: 100%
    
  &__body
    height: 100%
    display: grid
    grid-template-columns: repeat(2, 1fr)
    gap: 24px 

  &__item
    min-height: 240px
    padding: 24px 40px
    display: flex
    justify-content: flex-end
    background-position: center
    background-repeat: no-repeat
    background-size: cover

    &--left

      .promo__link
        color: var(--main-blue)
        &::before      
          background-color: var(--main-blue)
      
    &--right      
      color: var(--main-white)
      .promo__link        
        &::before      
          background-color: var(--main-white)

  &__content
    max-width: 248px
    display: flex
    flex-direction: column

  &__title
    font-size: 24px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.24px
    margin-bottom: 8px
    font-family: var(--secondary-font-family)

  &__descr
    font-size: 14px
    line-height: 160%
    margin-bottom: 44px
    flex: 1 1 auto

  &__link
    position: relative 
    display: inline 
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px
    &::before
      position: absolute
      content: ''
      bottom: -4px
      left: 0
      width: 100%
      height: 2px
      



