.modal
  position: fixed
  top: 0
  left: 0
  height: 100%
  width: 100%
  background-color: rgba(0, 0, 0, 0.7)
  display: flex
  align-items: center
  justify-content: center
  z-index: 1000
  opacity: 0
  visibility: hidden 
  transition: opacity 0.4s ease 0s, visibility 0.4s ease 0s
  &.active
      opacity: 1
      visibility: visible

  &__city,   
  &__callback   
    display: none
    border-radius: 8px
    background: var(--main-white)
    padding: 32px    
    &.active
      display: block

  &__callback    
    max-width: 440px

.modal-city  

  &__search
    display: flex
    align-items: center
    margin-bottom: 24px

  &__options  
    height: 425px  

.modal-top
  margin-bottom: 24px
  display: flex
  align-items: center
  justify-content: space-between

.modal-title
  font-size: 24px
  font-weight: 500
  line-height: 120%
  letter-spacing: -0.24px

.modal-close
  width: 48px
  height: 48px
  border-radius: 8px
  border: 1px solid rgba(51, 51, 51, 0.16)
  background-color: var(--main-white)
  svg
    width: 24px
    height: 24px
    fill: var(--main-gray)

.search-city

  &__form
    width: 473px
    height: 56px
    padding: 16px
    margin-right: 16px

  &__list
    display: flex
    align-items: center

  &__item
    &:not(&:last-child)
      margin-right: 6px

  &__link
    padding: 4px 12px
    color: var(--main-gray)
    font-size: 14px    
    font-weight: 400
    line-height: 160%
    border-radius: 999px
    background: rgba(51, 51, 51, 0.08)

  &__body
    position: relative

  &__results  
    height: 198px
    position: absolute  
    width: calc(100% - 16px)
    top: 63px
    left: 0
    opacity: 0
    visibility: hidden
    border-radius: 6px
    overflow-y: auto 
    background-color: var(--main-white)
    box-shadow: 0px 16px 24px 0px #ECE5DF
    padding: 8px
    transition: opacity 0.4s ease, visability 0.4s ease
    &.active
      opacity: 1
      visibility: visible

.simplebar-track.simplebar-vertical
  width: 6px

.simplebar-scrollbar:before,
.simplebar-scrollbar.simplebar-visible:before
  border-radius: 999px
  background-color: var(--light-gray)      

.simplebar-scrollbar.simplebar-visible:before      
  opacity: 1

.results-city

  &__item
    &:not(&:last-child)
      margin-bottom: 8px

  &__link
    display: block
    padding: 4px 12px
    font-size: 14px
    line-height: 160%
    border-radius: 4px
    transition: background-color 0.3s ease 
    @media (any-hover: hover)
      &:hover
        background-color: rgba(51, 51, 51, 0.08)
    

.options-city

  &__body
    height: 100%
    display: flex
    flex-direction: column    
    flex-wrap: wrap    
    margin: 0 -12px

  &__unit
    display: flex
    align-items: flex-start
    width: 280px
    margin: 0 12px
    &:not(&:last-child)
      margin-bottom: 16px

  &__letter
    flex: 0 0 24px
    font-size: 18px
    font-weight: 500
    line-height: 130%
    letter-spacing: -0.18px
    text-align: center
    margin-right: 8px

    &--flag
      height: 24px
      background-image: url('../img/icons/ru-flag.svg')  
      background-position: 2px 0px
      background-repeat: no-repeat
      background-size: 22px 22px
      

  &__list
    display: flex
    flex-direction: column
    align-items: flex-start

  &__item
    &:not(&:last-child)
      margin-bottom: 8px

  &__link
    padding: 4px 16px
    font-size: 14px
    line-height: 160%
    border-radius: 4px
    transition: background-color 0.3s ease 
    @media (any-hover: hover)
      &:hover    
        background-color:  #F2F2F2

.form-modal  

  &__body
    flex: 1 1 252px
    padding: 32px
    margin-bottom: 24px
    border-radius: 8px
    background-color: #F2F2F2

  &__label
    display: block
    width: 312px    
    &:not(&:last-child)
      margin-bottom: 16px
      
    span
      display: block
      color: var(--main-gray)
      font-size: 14px
      line-height: 160%
      margin-bottom: 8px


  &__input
    display: block
    width: 100%
    height: 56px
    background-color: var(--main-white)
    padding: 16px
    border-radius: 8px
    border: 1px solid var(--main-white)
    transition: border-color 0.3s ease
    outline: none
    &:focus
      border-color: var(--dark-color)


  &__required
    display: block
    color: var(--main-gray)
    font-size: 14px
    line-height: 160%
    margin-bottom: 24px

  &__btn
    width: 100%
    padding: 16px
    color: var(--main-white)
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px
    border-radius: 8px
    opacity: 0.3
    background: var(--main-blue)
    margin-bottom: 16px

  &__checkbox-label
    display: flex
    align-items: flex-start

  &__checkbox
    appearance: none
    &:checked + .form-modal__tag::after
      transform: scale(1)

    &:focus-visible + .form-modal__tag
      border-color: var(--main-blue)

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button
      -webkit-appearance: none
      margin: 0

  &__tag
    position: relative
    cursor: pointer
    display: block
    max-width: 352px
    color: var(--main-gray)
    font-size: 13px
    line-height: 140%
    padding-left: 25px
    &::before
      content: ''
      position: absolute
      top: 3px
      left: 0      
      width: 14px
      height: 14px
      border-radius: 2px
      border: 2px solid var(--main-blue)
    &::after      
      position: absolute
      content: ''
      top: 3px
      left: 0px
      width: 14px
      height: 14px      
      background: url('../img/icons/check.svg') center/cover no-repeat
      transform: scale(0)
      transition: transform 0.3 ease 

  &__link
    display: inline-block
    position: relative
    &::before      
      position: absolute
      content: ''
      bottom: -1px
      left: 0
      width: 100%
      height: 1px
      background-color: var(--main-gray)


	
    