*
  padding: 0
  margin: 0
  border: 0

*,
*::before,
*::after
  box-sizing: border-box

a:focus-visible,
button:focus-visible
  outline: 2px solid var(--main-blue)

a
  color: inherit
  outline: none

aside,
nav,
footer,
header,
section
  display: block

html,
body
  height: 100%
  min-width: var(--min-width)  

body
  font-size: 16px
  line-height: 1
  font-family: var(--main-font-family)
  color: var(--dark-color)
  background-color: var(--main-white)
  font-weight: 400
  font-style: normal

input,
button,
textarea
  font-size: inherit
  font-family: var(--main-font-family)

button
  cursor: pointer
  background-color: transparent

a,
a:visited
  text-decoration: none

a:hover
  text-decoration: none

ul li
  list-style: none

img
  vertical-align: top

h1,
h2,
h3,
h4,
h5,
h6
  font-weight: inherit
  font-size: inherit

.wrapper
  width: 100%
  min-height: 100%
  overflow: hidden
  display: flex
  flex-direction: column

//Sections  
.main
  flex: 1 1 auto	  

  &__hero
    padding-top: 256px 

  &__subcategories,
  &__salons,
  &__interiors,
  &__encyclopedia
    padding: 56px 0

  &__bestsellers,
  &__questions
    padding-top: 56px

  &__about
    padding: 80px 0

  &__newsellers
    padding-bottom: 56px  

//Container
.container
  max-width: var(--max-width-container)
  padding: 0 15px
  margin: 0 auto
  @media (max-width: var(--md3))
    padding: 0 10px

//Modal 
.show
  display: block

.hide
  display: none

.fade
  animation-name: fade
  animation-duration: 1.5s

@keyframes fade
  from
    opacity: 0.1
  to
    opacity: 1

//Btn
.btn
  display: inline-flex
  align-items: center
  justify-content: center

.btn-icon
  width: 48px
  height: 48px
  border-radius: 8px
  border: 1px solid rgba(51, 51, 51, 0.16)  
  svg
    width: 24px
    height: 24px
    fill: var(--light-gray)
    transition: fill 0.3s ease

  &--close
    svg
      width: 18px
      height: 18px
  
  @media (any-hover: hover)
    &:hover           
      svg
        fill: var(--main-gray)    

.btn-blue
  color: var(--main-white)
  font-weight: 500
  line-height: 120%
  letter-spacing: -0.16px
  border-radius: 8px
  background-color: var(--main-blue)  
  transition: all 0.3s ease 0s
  &.disabled
    opacity: 0.3
    cursor: not-allowed
  @media (any-hover: hover)
    &:hover
      background-color: var(--main-electric-blue)

.slider-btn
  width: 48px
  height: 48px
  border-radius: 8px
  transition: background-color 0.3s ease 
  svg
    width: 24px
    height: 24px

  &--next      
    svg
      transform: rotate(180deg)

//Links
.link-blue
  color: var(--main-blue)  
  transition: color 0.3s ease
  svg
    fill: var(--main-blue)
    transition: fill 0.3s ease
  @media (any-hover: hover)
    &:hover
      color: var(--main-electric-blue)
      svg
        fill: var(--main-electric-blue)

//Elements
.point-element
  position: relative    
  &::after
    position: absolute
    content: ''
    top: 50%
    right: -12px
    transform: translateY(-50%)
    width: 2px
    height: 2px
    border-radius: 100%
    background-color: var(--light-gray)

.time-element
  display: block
  padding-left: 20px         
  background-position: 0px 0px
  background-repeat: no-repeat
  background-size: 16px 16px
  background-image: url('../img/icons/clock.svg')


//Hidden Class
.hidden
  overflow: hidden

//Title
.title
  font-size: 32px
  font-weight: 500
  line-height: 120%
  letter-spacing: -0.32px
  font-family: var(--secondary-font-family)

.title-btn
  color: var(--main-blue)
  font-family: var(--secondary-font-family)

//Slider Pagination
.slider-pagination.swiper-horizontal>.swiper-pagination-progressbar, .swiper-pagination-progressbar.swiper-pagination-horizontal
  position: relative    
  height: 2px
  background-color: rgba(51, 51, 51, 0.08)
  z-index: 50 

//Product
.product-item
  display: flex
  flex-direction: column
  width: 288px
  height: 100%
  background-color: var(--main-white)

  &__img
    position: relative
    height: 190px
    padding: 18px 28px
    background-color: #f5f3f1
    margin-bottom: 12px
    overflow: hidden
    img      
      height: 100%
      width: 100%
      object-fit: cover

  &__labels
    position: absolute
    top: 0
    left: -3px
    display: flex
    align-items: center

  &__label
    display: block
    padding: 3px 6px
    font-size: 11px
    font-weight: 500
    transform: skewX(-15deg)
    span
      display: block
      transform: skewX(15deg)

    &--sale
      background-color: var(--accent-dark-orange)
      color: var(--main-white)      

    &--best
      background-color: var(--accent-light-orange)
      color: var(--main-white)      

    &--new      
      background-color: var(--main-beidge)

  &__favorite-btn
    position: absolute
    top: 8px
    right: 8px
    width: 40px
    height: 40px
    border-radius: 100%
    background-color: var(--main-white)    
    svg
      width: 24px
      height: 24px
      fill: var(--light-gray)
  
  &__content
    flex: 1 1 auto
    display: flex
    flex-direction: column

  &__title 
    flex: 1 1 auto
    margin-bottom: 12px   

  &__link
    display: block
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px

  &__center
    display: flex
    align-items: center  
    margin-bottom: 13px

  &__rating    
    padding-left: 20px
    background-image: url('../img/bestsellers/star-rate.svg') 
    background-position: 0px 3px
    background-repeat: no-repeat
    background-size: 16px 16px
    margin-right: 24px 

  &__review
    color: var(--main-blue)    

  &__availability
    position: relative
    margin-left: auto
    color: var(--accent-light-green)
    &::before
      position: absolute
      content: ''
      top: 50%
      transform: translateY(-50%)
      left: -10px
      width: 6px
      height: 6px
      border-radius: 100%
      background-color: var(--accent-light-green)

  &__textile
    display: flex
    align-items: center
    margin-bottom: 12px

  &__hardness
    display: flex
    align-items: center
    color: var(--light-gray)
    margin-bottom: 12px
    span
      display: block
      color: var(--dark-color)
    svg
      width: 24px
      height: 24px
      fill: var(--dark-color)
      margin: 0 2px

  &__bottom

  &__select
    display: flex
    align-items: center
    justify-content: space-between
    margin-bottom: 13px

  &__prices
    display: flex
    align-items: center 

  &__price
    display: block

    &--new
      font-size: 18px
      font-weight: 500
      line-height: 130%
      letter-spacing: -0.18px
      margin-right: 12px

    &--old
      color: var(--light-gray)
      line-height: 120%
      letter-spacing: -0.16px
      text-decoration: line-through
      margin-right: 18px

  &__discount
    position: relative
    color: var(--main-white)
    background-color: var(--accent-dark-orange)
    padding-right: 4px
    &::before
      position: absolute
      content: ''
      top: 1px
      left: -6px
      width: 6px
      height: 20px
      background: url('../img/bestsellers/discount-element.svg') center/cover no-repeat   

  &__hover
    margin-top: 16px
    display: none

.textile-product

  &__name,
  &__more
    display: block    
    color: var(--light-gray)

  &__name
    margin-right: 6px

  &__btns
    flex: 0 0 80px
    display: flex
    align-items: center
    margin-right: 8px

  &__label
    position: relative
    cursor: pointer
    display: block    
    flex: 0 0 20px
    @media (any-hover: hover)
      &:hover
        .textile-product__tooltip
          opacity: 1
          visibility: visible
      

  &__custom
    display: block
    width: 100%      
    position: relative
    &::before
      position: absolute
      content: ''
      top: -13px
      left: 4px
      width: 12px
      height: 12px
      border-radius: 100%
    &::after
      position: absolute
      content: ''
      top: -17px
      left: 0
      width: 20px
      height: 20px
      border-radius: 100%
      border: 1px solid var(--light-gray)
      opacity: 0
      visibility: hidden
      transition: opacity 0.3s ease, visibility 0.3s ease

    &--1      
      &::before
        background: url('../img/bestsellers/radio-1.png') center/cover  no-repeat

    &--2      
      &::before
        background: url('../img/bestsellers/radio-2.png') center/cover  no-repeat

    &--3      
      &::before
        background: url('../img/bestsellers/radio-3.png') center/cover  no-repeat

    &--4      
      &::before
        background: url('../img/bestsellers/radio-4.png') center/cover  no-repeat
        
    &:not(&:last-child)
      margin-right: 4px

  &__radio
    appearance: none
    &:checked + .textile-product__custom::after
      opacity: 1
      visibility: visible    

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button
      -webkit-appearance: none
      margin: 0

  &__tooltip
    display: block
    opacity: 0
    visibility: hidden
    position: absolute    
    top: -38px
    left: 0    
    white-space: nowrap
    color: var(--main-gray)
    font-size: 11px
    font-weight: 500
    line-height: 140%
    padding: 8px 16px
    border-radius: 4px 4px 4px 0px
    background-color: var(--main-white)  
    box-shadow: 0 0 1px 0 currentColor, inset 0 0 1px 0 currentColor  
    box-shadow: 0px 16px 24px 0px #ECE5DF
    transition: opacity 0.3s ease, visibility 0.3s ease
    z-index: 5

.select-product

  &__size
    display: block
    color: var(--light-gray)
    margin-right: 13px
    white-space: nowrap

  &__body
    flex: 0 0 211px
    height: 38px
    display: flex
    align-items: center
    justify-content: space-between
    padding: 8px 12px
    border-radius: 4px
    border: 1px solid rgba(51, 51, 51, 0.16)
    svg
      width: 20px
      height: 20px
      fill: var(--dark-color)

.hover-product

  &__btns
    display: flex
    align-items: center
    justify-content: space-between
    margin-bottom: 16px

  &__add-btn    
    padding: 8px 66px
    svg
      width: 24px
      height: 24px
      fill: var(--main-white)
      margin-right: 8px

  &__compare-btn
    width: 40px
    height: 40px
    border-radius: 100%
    background-color: #F2F2F2
    svg
      width: 24px
      height: 24px
      fill: var(--light-gray)

  &__descr
    color: var(--main-gray)
    margin-bottom: 16px

  &__textile
    display: flex
    align-items: center
    margin-bottom: 16px

  &__delivery
    color: var(--main-gray)
    display: flex
    align-items: center
    svg
      width: 20px
      height: 20px
      fill: var(--main-gray)
      margin-right: 8px
    span
      display: block
      position: relative
      color: var(--dark-color)
      padding-left: 18px
      &::before
        position: absolute
        content: ''
        top: 50%
        transform: translateY(-50%)
        left: 6px
        width: 7px
        height: 2px
        background-color: var(--dark-color)

  &__feature
    display: grid
    grid-template-columns: repeat(3, 1fr)
    gap: 24px
    margin-bottom: 16px

.feature-hover

  &__column


  &__name
    display: block
    color: var(--main-gray)
    font-size: 11px
    font-weight: 500
    line-height: 140%
    margin-bottom: 4px

  &__descr
    display: block
    font-size: 13px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.13px

    &--blue
      color: var(--main-blue)

.textile-hover

  &__name
    margin-right: 9px

  &__name,
  &__more   
    display: block 
    color: var(--light-gray)
    font-size: 13px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.13px

  &__types
    display: flex
    align-items: center
    margin-right: 11px

  &__type
    display: block
    width: 12px
    height: 12px
    border-radius: 100%
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    &:not(&:last-child)
      margin-right: 12px


//Text class
.product-text
  font-size: 14px
  line-height: 160%

//Clamp Text
.text-clamp
  overflow: hidden
  display: -webkit-box
  -webkit-line-clamp: 2
  -webkit-box-orient: vertical


