.encyclopedia
  min-height: 786px
  background-position: center
  background-repeat: no-repeat
  background-size: cover

  &__top
    display: flex
    align-items: flex-end
    margin-bottom: 32px

  &__left
    flex: 1 1 auto
    margin-right: 15px

  &__title
    margin-bottom: 16px

  &__all
    display: flex
    align-items: center
    margin-bottom: 16px
    svg
      width: 14px
      height: 14px      
      margin-left: 5px

  &__descr
    line-height: 160%
    max-width: 650px

  &__slider

  &__body
    margin-right: -56px

  &__wrapper
    align-items: stretch
    margin-bottom: 32px

  &__slide   
    height: auto 

  &__pagination.swiper-horizontal>.swiper-pagination-progressbar, .swiper-pagination-progressbar.swiper-pagination-horizontal
    width: calc(100% - 56px)    

.encyclopedia-slide

  &__link
    display: flex
    flex-direction: column
    height: 100%
    width: 288px
    border-radius: 8px    
    @media (any-hover: hover)
      &:hover
        .encyclopedia-slide__btn
          opacity: 1
          visibility: visible

        .encyclopedia-slide__img    
          flex: 0 0 234px

        .encyclopedia-slide__content
          padding-bottom: 16px
          flex: 0 0 222px
    

  &__img
    position: relative
    flex: 0 1 284px
    width: 100%
    border-radius: 8px 8px 0px 0px
    overflow: hidden
    transition: flex 0.3s ease
    img
      width: 100%
      height: 100%
      object-fit: cover
      border-radius: 8px 8px 0px 0px

  &__label
    position: absolute
    bottom: 0
    left: -2px
    display: block
    color: var(--main-white)
    background-color: var(--main-blue)
    padding: 3px 6px
    font-size: 11px
    font-weight: 500
    transform: skewX(-15deg)
    span
      display: block
      transform: skewX(15deg)

  &__content
    flex: 1 1 172px
    display: flex
    flex-direction: column
    padding: 16px 24px 0px 24px
    background-color: var(--main-white)
    border-radius: 0px 0px 8px 8px
    transition: flex 0.3s ease, padding 0.3s ease

  &__top
    display: flex
    align-items: center
    color: var(--light-gray)
    font-size: 13px
    line-height: 120%
    letter-spacing: -0.13px
    margin-bottom: 8px

  &__date
    display: block
    margin-right: 20px

  &__title
    flex: 1 1 auto
    font-size: 18px
    font-weight: 500
    line-height: 130%
    letter-spacing: -0.18px

  &__btn
    opacity: 0
    visibility: hidden
    padding: 10px 33px
    transition: opacity 0.3s ease, visability 0.3s ease
    svg
      margin-left: 9px
      width: 18px
      height: 18px
      fill: var(--main-white)


