:root {
  --main-font-family: "EuclidCircularA", sans-serif;
  --secondary-font-family: "CloserText", sans-serif;
  --min-width: 320px;
  --max-width-container: 1254px;
  --md-xl: 1200px;
  --md-l: 992px;
  --md-m: 768px;
  --md-s: 480px;
  --dark-color:#333333;
  --main-blue: #0A4881;
  --main-electric-blue: #0063BD;
  --light-gray: #B3B3B3;
  --main-gray: #808080;
  --border-color: #E4EBF1;
  --main-beidge: #D7CCC3;
  --light-beidge: #F5F3F1;
  --medium-beidge: #ECE5DF;
  --main-white: #FFF;
  --accent-dark-orange: #D44A1B;
  --accent-light-orange: #EE7E19;
  --accent-light-green: #7BBF3F;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid var(--main-blue);
}

a {
  color: inherit;
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: var(--min-width);
}

body {
  font-size: 16px;
  line-height: 1;
  font-family: var(--main-font-family);
  color: var(--dark-color);
  background-color: var(--main-white);
  font-weight: 400;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: var(--main-font-family);
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}
.main__hero {
  padding-top: 256px;
}
.main__subcategories, .main__salons, .main__interiors, .main__encyclopedia {
  padding: 56px 0;
}
.main__bestsellers, .main__questions {
  padding-top: 56px;
}
.main__about {
  padding: 80px 0;
}
.main__newsellers {
  padding-bottom: 56px;
}

.container {
  max-width: var(--max-width-container);
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: var(--md3)) {
  .container {
    padding: 0 10px;
  }
}

.show {
  display: block;
}

.hide {
  display: none;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-icon {
  width: 48px;
  height: 48px;
  border-radius: 8px;
  border: 1px solid rgba(51, 51, 51, 0.16);
}
.btn-icon svg {
  width: 24px;
  height: 24px;
  fill: var(--light-gray);
  transition: fill 0.3s ease;
}
.btn-icon--close svg {
  width: 18px;
  height: 18px;
}
@media (any-hover: hover) {
  .btn-icon:hover svg {
    fill: var(--main-gray);
  }
}

.btn-blue {
  color: var(--main-white);
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.16px;
  border-radius: 8px;
  background-color: var(--main-blue);
  transition: all 0.3s ease 0s;
}
.btn-blue.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
@media (any-hover: hover) {
  .btn-blue:hover {
    background-color: var(--main-electric-blue);
  }
}

.slider-btn {
  width: 48px;
  height: 48px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}
.slider-btn svg {
  width: 24px;
  height: 24px;
}
.slider-btn--next svg {
  transform: rotate(180deg);
}

.link-blue {
  color: var(--main-blue);
  transition: color 0.3s ease;
}
.link-blue svg {
  fill: var(--main-blue);
  transition: fill 0.3s ease;
}
@media (any-hover: hover) {
  .link-blue:hover {
    color: var(--main-electric-blue);
  }
  .link-blue:hover svg {
    fill: var(--main-electric-blue);
  }
}

.point-element {
  position: relative;
}
.point-element::after {
  position: absolute;
  content: "";
  top: 50%;
  right: -12px;
  transform: translateY(-50%);
  width: 2px;
  height: 2px;
  border-radius: 100%;
  background-color: var(--light-gray);
}

.time-element {
  display: block;
  padding-left: 20px;
  background-position: 0px 0px;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-image: url("../img/icons/clock.svg");
}

.hidden {
  overflow: hidden;
}

.title {
  font-size: 32px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.32px;
  font-family: var(--secondary-font-family);
}

.title-btn {
  color: var(--main-blue);
  font-family: var(--secondary-font-family);
}

.slider-pagination.swiper-horizontal > .swiper-pagination-progressbar, .swiper-pagination-progressbar.swiper-pagination-horizontal {
  position: relative;
  height: 2px;
  background-color: rgba(51, 51, 51, 0.08);
  z-index: 50;
}

.product-item {
  display: flex;
  flex-direction: column;
  width: 288px;
  height: 100%;
  background-color: var(--main-white);
}
.product-item__img {
  position: relative;
  height: 190px;
  padding: 18px 28px;
  background-color: #f5f3f1;
  margin-bottom: 12px;
  overflow: hidden;
}
.product-item__img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.product-item__labels {
  position: absolute;
  top: 0;
  left: -3px;
  display: flex;
  align-items: center;
}
.product-item__label {
  display: block;
  padding: 3px 6px;
  font-size: 11px;
  font-weight: 500;
  transform: skewX(-15deg);
}
.product-item__label span {
  display: block;
  transform: skewX(15deg);
}
.product-item__label--sale {
  background-color: var(--accent-dark-orange);
  color: var(--main-white);
}
.product-item__label--best {
  background-color: var(--accent-light-orange);
  color: var(--main-white);
}
.product-item__label--new {
  background-color: var(--main-beidge);
}
.product-item__favorite-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background-color: var(--main-white);
}
.product-item__favorite-btn svg {
  width: 24px;
  height: 24px;
  fill: var(--light-gray);
}
.product-item__content {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}
.product-item__title {
  flex: 1 1 auto;
  margin-bottom: 12px;
}
.product-item__link {
  display: block;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.16px;
}
.product-item__center {
  display: flex;
  align-items: center;
  margin-bottom: 13px;
}
.product-item__rating {
  padding-left: 20px;
  background-image: url("../img/bestsellers/star-rate.svg");
  background-position: 0px 3px;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  margin-right: 24px;
}
.product-item__review {
  color: var(--main-blue);
}
.product-item__availability {
  position: relative;
  margin-left: auto;
  color: var(--accent-light-green);
}
.product-item__availability::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: -10px;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  background-color: var(--accent-light-green);
}
.product-item__textile {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
.product-item__hardness {
  display: flex;
  align-items: center;
  color: var(--light-gray);
  margin-bottom: 12px;
}
.product-item__hardness span {
  display: block;
  color: var(--dark-color);
}
.product-item__hardness svg {
  width: 24px;
  height: 24px;
  fill: var(--dark-color);
  margin: 0 2px;
}
.product-item__select {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 13px;
}
.product-item__prices {
  display: flex;
  align-items: center;
}
.product-item__price {
  display: block;
}
.product-item__price--new {
  font-size: 18px;
  font-weight: 500;
  line-height: 130%;
  letter-spacing: -0.18px;
  margin-right: 12px;
}
.product-item__price--old {
  color: var(--light-gray);
  line-height: 120%;
  letter-spacing: -0.16px;
  text-decoration: line-through;
  margin-right: 18px;
}
.product-item__discount {
  position: relative;
  color: var(--main-white);
  background-color: var(--accent-dark-orange);
  padding-right: 4px;
}
.product-item__discount::before {
  position: absolute;
  content: "";
  top: 1px;
  left: -6px;
  width: 6px;
  height: 20px;
  background: url("../img/bestsellers/discount-element.svg") center/cover no-repeat;
}
.product-item__hover {
  margin-top: 16px;
  display: none;
}

.textile-product__name, .textile-product__more {
  display: block;
  color: var(--light-gray);
}
.textile-product__name {
  margin-right: 6px;
}
.textile-product__btns {
  flex: 0 0 80px;
  display: flex;
  align-items: center;
  margin-right: 8px;
}
.textile-product__label {
  position: relative;
  cursor: pointer;
  display: block;
  flex: 0 0 20px;
}
@media (any-hover: hover) {
  .textile-product__label:hover .textile-product__tooltip {
    opacity: 1;
    visibility: visible;
  }
}
.textile-product__custom {
  display: block;
  width: 100%;
  position: relative;
}
.textile-product__custom::before {
  position: absolute;
  content: "";
  top: -13px;
  left: 4px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
}
.textile-product__custom::after {
  position: absolute;
  content: "";
  top: -17px;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  border: 1px solid var(--light-gray);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.textile-product__custom--1::before {
  background: url("../img/bestsellers/radio-1.png") center/cover no-repeat;
}
.textile-product__custom--2::before {
  background: url("../img/bestsellers/radio-2.png") center/cover no-repeat;
}
.textile-product__custom--3::before {
  background: url("../img/bestsellers/radio-3.png") center/cover no-repeat;
}
.textile-product__custom--4::before {
  background: url("../img/bestsellers/radio-4.png") center/cover no-repeat;
}
.textile-product__custom:not(.textile-product__custom:last-child) {
  margin-right: 4px;
}
.textile-product__radio {
  appearance: none;
}
.textile-product__radio:checked + .textile-product__custom::after {
  opacity: 1;
  visibility: visible;
}
.textile-product__radio input::-webkit-outer-spin-button,
.textile-product__radio input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.textile-product__tooltip {
  display: block;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: -38px;
  left: 0;
  white-space: nowrap;
  color: var(--main-gray);
  font-size: 11px;
  font-weight: 500;
  line-height: 140%;
  padding: 8px 16px;
  border-radius: 4px 4px 4px 0px;
  background-color: var(--main-white);
  box-shadow: 0 0 1px 0 currentColor, inset 0 0 1px 0 currentColor;
  box-shadow: 0px 16px 24px 0px #ECE5DF;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 5;
}

.select-product__size {
  display: block;
  color: var(--light-gray);
  margin-right: 13px;
  white-space: nowrap;
}
.select-product__body {
  flex: 0 0 211px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid rgba(51, 51, 51, 0.16);
}
.select-product__body svg {
  width: 20px;
  height: 20px;
  fill: var(--dark-color);
}

.hover-product__btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}
.hover-product__add-btn {
  padding: 8px 66px;
}
.hover-product__add-btn svg {
  width: 24px;
  height: 24px;
  fill: var(--main-white);
  margin-right: 8px;
}
.hover-product__compare-btn {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background-color: #F2F2F2;
}
.hover-product__compare-btn svg {
  width: 24px;
  height: 24px;
  fill: var(--light-gray);
}
.hover-product__descr {
  color: var(--main-gray);
  margin-bottom: 16px;
}
.hover-product__textile {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}
.hover-product__delivery {
  color: var(--main-gray);
  display: flex;
  align-items: center;
}
.hover-product__delivery svg {
  width: 20px;
  height: 20px;
  fill: var(--main-gray);
  margin-right: 8px;
}
.hover-product__delivery span {
  display: block;
  position: relative;
  color: var(--dark-color);
  padding-left: 18px;
}
.hover-product__delivery span::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 6px;
  width: 7px;
  height: 2px;
  background-color: var(--dark-color);
}
.hover-product__feature {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  margin-bottom: 16px;
}

.feature-hover__name {
  display: block;
  color: var(--main-gray);
  font-size: 11px;
  font-weight: 500;
  line-height: 140%;
  margin-bottom: 4px;
}
.feature-hover__descr {
  display: block;
  font-size: 13px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.13px;
}
.feature-hover__descr--blue {
  color: var(--main-blue);
}

.textile-hover__name {
  margin-right: 9px;
}
.textile-hover__name, .textile-hover__more {
  display: block;
  color: var(--light-gray);
  font-size: 13px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.13px;
}
.textile-hover__types {
  display: flex;
  align-items: center;
  margin-right: 11px;
}
.textile-hover__type {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.textile-hover__type:not(.textile-hover__type:last-child) {
  margin-right: 12px;
}

.product-text {
  font-size: 14px;
  line-height: 160%;
}

.text-clamp {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

@font-face {
  font-family: "EuclidCircularA";
  src: url("../fonts/EuclidCircularA-Regular.woff2") format("woff2"), url("../EuclidCircularA-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "EuclidCircularA";
  src: url("../fonts/EuclidCircularA-Medium.woff2") format("woff2"), url("../EuclidCircularA-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "CloserText";
  src: url("../fonts/CloserText-Medium.woff2") format("woff2"), url("../CloserText-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
.header {
  min-height: 208px;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 500;
}
.header__special {
  display: block;
  text-align: center;
  background-color: var(--main-blue);
  color: var(--main-white);
  font-size: 13px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.13px;
  padding: 4px 0;
}
.header__container {
  background-color: var(--main-white);
}
.header__top {
  display: flex;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid var(--border-color);
}
.header__center {
  position: relative;
  display: flex;
  align-items: center;
  padding: 23px 0;
}
.header__bottom {
  position: relative;
}

.top-header__location {
  display: flex;
  align-items: center;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
}
@media (any-hover: hover) {
  .top-header__location:hover .top-header__city {
    color: var(--main-blue);
  }
}
.top-header__location svg {
  width: 16px;
  height: 16px;
  fill: var(--main-blue);
  margin-right: 4px;
}
.top-header__city {
  margin-right: 9px;
  transition: color 0.3s ease;
}
.top-header__quantity {
  position: relative;
  color: var(--light-gray);
  white-space: nowrap;
  margin-right: 32px;
}
.top-header__quantity::before {
  position: absolute;
  content: "";
  top: 50%;
  left: -5px;
  transform: translateY(-50%);
  width: 2px;
  height: 2px;
  border-radius: 100%;
  background-color: var(--light-gray);
}
.top-header__menu {
  margin-right: 69px;
}
.top-header__list {
  display: flex;
  align-items: center;
}
.top-header__item:not(.top-header__item:last-child) {
  margin-right: 32px;
}
.top-header__item--submenu {
  position: relative;
}
.top-header__link {
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
  line-height: 120%;
  white-space: nowrap;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .top-header__link:hover {
    color: var(--main-blue);
  }
  .top-header__link:hover span {
    color: var(--main-blue);
  }
  .top-header__link:hover svg {
    fill: var(--main-blue);
  }
}
.top-header__link--element {
  position: relative;
  padding-left: 20px;
}
.top-header__link--element::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 2px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-color: rgba(212, 74, 27, 0.2);
}
.top-header__link--element::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 6px;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: var(--accent-dark-orange);
}
.top-header__link--submenu {
  display: flex;
  align-items: center;
  line-height: 110%;
}
.top-header__link--submenu span {
  color: var(--light-gray);
  margin-right: 2px;
  transition: color 0.3s ease;
}
.top-header__link--submenu svg {
  width: 16px;
  height: 16px;
  fill: var(--light-gray);
  transition: fill 0.3s ease, transform 0.3s ease;
}
.top-header__link--submenu.active span {
  color: var(--main-blue);
}
.top-header__link--submenu.active svg {
  transform: rotate(-180deg);
  fill: var(--main-blue);
}
.top-header__contacts {
  top: 24px;
  right: 0;
}
.top-header__submenu {
  top: 24px;
  left: 0;
}
.top-header__sublist {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 8px;
}
.top-header__subitem {
  width: 100%;
}
.top-header__subitem:not(.top-header__subitem:last-child) {
  margin-bottom: 8px;
}
.top-header__sublink {
  display: block;
  width: 100%;
  text-align: left;
  white-space: nowrap;
  padding: 5px 19px;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}
@media (any-hover: hover) {
  .top-header__sublink:hover {
    background-color: rgba(51, 51, 51, 0.08);
  }
}
.top-header__phones {
  position: relative;
  margin-right: 24px;
}
.top-header__phones-btn {
  display: flex;
  align-items: center;
}
.top-header__phones-btn.active span {
  color: var(--main-blue);
}
.top-header__phones-btn.active svg {
  transform: rotate(-180deg);
  fill: var(--main-blue);
}
@media (any-hover: hover) {
  .top-header__phones-btn:hover span {
    color: var(--main-blue);
  }
  .top-header__phones-btn:hover svg {
    fill: var(--main-blue);
  }
}
.top-header__phones-btn span {
  color: var(--dark-color);
  font-weight: 500;
  letter-spacing: -0.16px;
  margin-right: 2px;
