.hero
  min-height: 586px
  
  &__sliders
    display: flex

  &__slider
    min-width: 0

    &--left
      position: relative
      flex: 0 1 912px
      margin-right: 24px      

    &--right
      position: relative
      flex: 0 0 288px      

.slider-left

  &__link
    img
      width: 100%
      height: 100%
      object-fit: cover

  &__pagination.swiper-horizontal>.swiper-pagination-progressbar, .swiper-pagination-progressbar.swiper-pagination-horizontal
    position: relative    
    height: 2px
    background-color: rgba(51, 51, 51, 0.08)
    top: 8px
    left: 16px
    width: 880px
    z-index: 50    

  &__btns
    width: 100%

  &__btn
    position: absolute
    top: 50%
    transform: translateY(-50%)    
    background-color: rgba(255, 255, 255, 0.40)
    z-index: 10    
    svg      
      fill: var(--main-blue)      
    @media (any-hover: hover)
      &:hover
        background-color: var(--main-white)    
    
    &--prev
      left: 16px

    &--next
      right: 16px
      svg
        transform: rotate(180deg)

.slider-right

  .swiper-wrapper
    height: 97%

  &__body
    width: 100%
    height: 103%

  &__link
    width: 100%
    height: 100%
    display: flex
    flex-direction: column
    justify-content: space-between
    padding: 24px 24px 20px 24px
    background-position: center
    background-repeat: no-repeat
    background-size: cover    

  &__top
    display: flex
    align-items: center
    justify-content: space-between
    color: var(--main-white)
    font-size: 11px
    font-weight: 500
    line-height: 140%

  &__date
    display: flex
    align-items: center
    color: var(--light-gray)
    font-size: 13px
    line-height: 120%
    letter-spacing: -0.13px
    margin-bottom: 8px

  &__day    
    display: block
    margin-right: 20px    

  &__time 

  &__descr
    color: var(--main-white)
    font-size: 18px
    font-weight: 500
    line-height: 130%
    letter-spacing: -0.18px     

  &__pagination.swiper-horizontal>.swiper-pagination-progressbar, .swiper-pagination-progressbar.swiper-pagination-horizontal
    position: relative   
    height: 2px
    background-color: rgba(51, 51, 51, 0.08)
    top: 8px
    left: 16px
    width: 256px
    z-index: 50

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill
  height: 2px
  background-color: var(--main-blue)  
  border-radius: 999px
  


